import React from "react";

const NewsItem = (props) => {
 
    let { title, description, imageUrl, newsUrl, author, date, source}=
    props;

    return (
      <div className="my-3">
        <div className="card">
        <div style={{display:"flex",
        position:"absolute",
        justifyContent:"flex-end",
        right:0
        }}>
        <span className="
        badge badge-pill bg-danger">
   {source}
    
  </span>
  </div>
          <img
            src={
              !imageUrl
                ? "https://i.guim.co.uk/img/media/f6f31ffa1d61ec9f0bcef027ac2b20e0315e421c/0_0_2800_1721/master/2800.jpg?width=300&quality=45&auto=format&fit=max&dpr=2&s=1cdc8b96424828957818816414979980"
                : imageUrl
            }
            className="card-img-top"
            alt="..."
          />
          <div className="card-body">
            
            <h5 className="card-title">{title}...</h5>
            <p className="card-text">{description}...</p>
            <p className="card-text">
              <small class="text-muted">
                by {!author ? "Unknown" : author} on{" "}
                {new Date(date).toLocaleString()}
              </small>
            </p>
            
            <a
              href={newsUrl}
              rel="noreferrer"
              target="_blank"
              className="btn btn-sm btn-warning"
            >
              Read more
            </a>
          </div>
        </div>
      </div>
    );
  
}

export default NewsItem;
